<template>
  <div class="jumbotron" id="Home">
    <div class="inner">
      <h1 class="py-3 Home-title" style="z-index:100">AI輔助親權判決預測網頁</h1>
      <div class="py-1">
        <UserGuide :mainIntro="mainIntro" :guide="guide" :isHome="true"></UserGuide>
      </div>
      <div id="btn-det" class="my-5">
        <div class="d-inline-flex w-100 justify-content-center">
          <div class="mt-4 px-2" style="z-index:100">
            <router-link to="/pastCase" class="btn-lg btn-default btn-navigate">開始閱讀案例 ></router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserGuide from '../Sub/userGuide';

export default {
  name: 'HomePage',
  components: {
    UserGuide,
  },
  data() {
    return {
      mainIntro:
        '本網頁是國立清華大學執行科技部AI人社領域計畫的成果展示與測試網頁。本計畫是使用司法院所公開釋出的判決書作為訓練資料，運用人工智慧中的自然語言處理技術建立人工智慧模型，針對若干司法案件預測可能的判決結果，提供案件的當事人參考。',
      guide:
        '一、本系統目前僅處理「離婚後父母雙方爭取孩子撫養權」的相關案例。<br />二、為增加預測結果的可靠度與避免錯誤使用，使用者請先閱讀相關案例。<br />三、本系統假設使用者所需要之輸入雙方與相關文字描述皆是正確與完整。若有資料提供不完整、錯誤、誇大或刻意偏袒等，結果可能會受影響。建議使用前可以自行多一些整理，或請公正第三方協助輸入。<br />四、本系統的功能與準確度亦隨時改進中，若有任何疑問與建議，歡迎留言指教。',
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="scss" scoped>
@import "vue-select/src/scss/vue-select.scss";
@import "~bootstrap/scss/_functions";
@import "~bootstrap/scss/_variables";
@import "~bootstrap/scss/mixins/_breakpoints";

* {
  position: relative;
  font-family: 微軟正黑體;
}

.container {
  @media (min-width: 1800px) {
    max-width: 1500px;
  }
}

.reasoningText {
  vertical-align: top;
  border: none;
  background-color: transparent;
}

.description {
  letter-spacing: 4px;
  line-height: 35px;
}
.jumbotron {
  padding: 2rem 1rem;
  margin-bottom: 0px;
  padding-bottom: 0px;
  border-radius: 0px;
}

.btn {
  color: white;
  background-color: gray;
  padding: 15px;
  letter-spacing: 2px;
  border-radius: 0px;
  border: none;
  font-weight: 800;
  font-size: 15px;
  transition: background-color 0.2s ease-in-out;
  &:hover {
    background: #d8235e;
  }
}
.btn-navigate {
  color: white;
  background-color: rgb(226, 145, 60);
  padding: 15px;
  letter-spacing: 2px;
  border-radius: 0px;
  border: none;
  font-weight: 800;
  font-size: 15px;
  transition: background-color 0.2s ease-in-out;
  &:hover {
    background: rgb(74, 163, 175);
  }
}

#Home {
  background-color: #f7f7f7;
  height: 100%;
  position: relative;
  color: black;
  overflow-y: auto;
  overflow-x: hidden;
}

.Home-title {
  color: black;
}

.user-instruction {
  z-index: 100;
  line-height: 35px;
  letter-spacing: 4px;
  // margin-left: 50%;
  // transform: translateX(-50%);
  text-align: left;
  margin-top: -25px;
}
</style>



