<template>
  <div id="container-guide" class="container" style="z-index: 100">
    <h6
      class="py-1 description"
      style="z-index:100"
      id="mainIntro"
      :class="{'home-description': isHome}"
    ><div v-html="mainIntro"></div></h6>
    <div id="guide">
      <h4 class="user-instruction-title" :class="{'home-user-instruction-title': isHome}">Instructions:</h4>
      <h6 class="user-instruction" :class="{'home-user-instruction': isHome}">
        <div v-html="guide"></div>
      </h6>
    </div>
  </div>
</template>

<script>
export default {
  name: 'userGuide',
  props: {
    mainIntro: '',
    guide: '',
    isHome: false,
  },
};
</script>

<style lang="scss" scoped>

@font-face {
  font-family: 'cwTeXKai';
  font-style: normal;
  font-weight: 500;
  src: url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.eot);
  src: url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.eot?#iefix) format('embedded-opentype'),
       url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.woff2) format('woff2'),
       url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.woff) format('woff'),
       url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.ttf) format('truetype');
}


#mainIntro {
  position: relative;
  font-family: 標楷體, DFKai-SB, 楷體, STKaiti, cwTeXKai;
  font-size: 20pt;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  padding: 20px;
  margin-top: 30px;
}

#guide {
  border-radius: 25px;
  padding: 20px;
  background-color: rgba(230, 215, 173, 0.815);
}

#container-guide {
  @media (min-width: 1400px) {
    max-width: 900px;
  }
  @media (min-width: 1000px) and (max-width: 1400px) {
    max-width: 700px;
  }
  @media (max-width: 1000px) {
    max-width: 500px;
  }
}
.description {
  letter-spacing: 4px;
  line-height: 30px;
}
.user-instruction {
  z-index: 100;
  line-height: 30px;
  // letter-spacing: 4px;
  // margin-left: 50%;
  // transform: translateX(-50%);
  text-align: left;
}
.home-description {
  letter-spacing: 4px;
  line-height: 35px;
  padding: 15px;
  font-size: 1.1rem;
}
.home-user-instruction {
  z-index: 100;
  line-height: 35px;
  letter-spacing: 4px;
  // margin-left: 50%;
  // transform: translateX(-50%);
  text-align: left;
  margin-top: 20px;
  font-size: 1.05rem;
}
.home-user-instruction-title {
  margin-top: 20px;
}
.user-instruction-title {
  text-align: left;
}
</style>



